<template>
  <div class="setting" v-show="visible">
    <div class="setting-wrapper">
      <dt class="setting-title"></dt>
      <div class="setting-content">
        <div class="setting-form">
          <dl
            class="setting-form-item"
            v-for="formItem in settingForm"
            :key="formItem.key"
          >
            <label class="setting-form-label">{{ formItem.label }}</label>
            <div v-if="formItem.type === 'radio'" class="setting-radio">
              <template v-for="radioItem in formItem.radioList">
                <input
                  :key="`radio-${formItem.key}-${radioItem.value}`"
                  v-model="settingData[formItem.key]"
                  type="radio"
                  :value="radioItem.value"
                />
                <span
                  class="setting-radio-label"
                  :key="`radio-${formItem.key}-${radioItem.value}-label`"
                >
                  {{ radioItem.label }}
                </span>
              </template>
            </div>
          </dl>
        </div>
        <button class="rule-btn" @click="showRule"></button>
      </div>
      <div class="setting-btns">
        <button @click="confirmFun"></button>
        <button @click="cancelFun"></button>
      </div>
    </div>
  </div>
</template>

<script>
import RgConfirm from "../confirm";
export default {
  name: "Setting",
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    data: {
      type: Object,
      default: () => ({}),
    },
    gameRuleData: {
      type: Object,
      default: () => ({}),
    },
  },
  data() {
    return {
      settingData: {
        speed: 1,
      },
      settingForm: [
        {
          label: "",
          key: "speed",
          type: "radio",
          radioList: [
            {
              label: "1",
              value: 1,
            },
            {
              label: "2",
              value: 2,
            },
            {
              label: "4",
              value: 4,
            },
            {
              label: "10",
              value: 10,
            },
            {
              label: "20",
              value: 20,
            },
          ],
        },
      ],
    };
  },
  computed: {
    visible: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit("input", val);
      },
    },
    rules() {
      const { dayTime, cookSalary, maxWaitCustomerNum = 6 } = this.gameRuleData;
      return [
        `W+7W1`,
        `D+7`,
        `H+24${dayTime / 24000}`,
        ` ${cookSalary}`,
        ``,
        "500",
        ` ${maxWaitCustomerNum} `,
        "",
        "",
        "",
        "5",
        "61",
        "",
      ];
    },
  },
  watch: {
    value(val) {
      if (val) {
        this.settingData = {
          speed: this.data.speed || 1,
        };
      }
    },
  },
  methods: {
    close() {
      this.visible = false;
    },
    confirmFun() {
      this.close();
      this.$emit("save", this.settingData);
    },
    cancelFun() {
      this.close();
      this.$emit("cancel");
    },
    showRule() {
      RgConfirm({
        title: "",
        content: `<ul style="max-height: calc(100vh - 20rem);overflow-y: auto;padding-left: 1em;">
          ${this.rules.map((el) => "<li>" + el + "</li>").join("")}
        </ul>`,
        cancelBtnShow: false,
      });
    },
  },
};
</script>

